export default {
	data() {
		return {
			load: true
		};
	},
	methods: {
		openCart(){
			uni.hideTabBar()
			this.$refs.popup.open();
		},
		onClose() {
			this.$refs.popup.close();
				uni.showTabBar()
		},
		shopDetailPage(){
			uni.navigateTo({
				url:'/pages/index/shopdetail'
			})
		}
	}
};
